/*style.scss*/
@import '../_config';
@import '../../MediaFiles/Fonts/fonts';


/*
    style.css
    Wezom wTPL v4.0.0
*/
/*-------------------------------  Defs & Sys  ------------------------------------*/
    body {
        position: relative;
        min-width: 320px;
        font-size: $fontSize;
        line-height: $emSizeText;
        color: $colorBlack;
        background-color: $colorWhite;
        -webkit-font-smoothing: antialiased;
    }
    body, button, input, select, textarea {
        font-family: $fontFamily;
    }
    a {
        color: $colorLink;
    }
    .w_tac { text-align: center; }
    .w_tar { text-align: right; }
    .w_tal { text-align: left; }
    .w_fll { float: left; }
    .w_flr { float: right; }
    .w_cls { font-size: 0; display: table; width: 100%;}
    .w_ovh { overflow: hidden; }
    .w_clear { clear: both;}
    .w_blank { height: 25px; }
    .w_first {margin-top: 0 !important;}
    .w_last {margin-bottom: 0 !important;}
    .w_clearfix, .wSize, .wTxt {
        &:after {
            content: '';
            display: block;
            clear: both;
            height: 0;
        }
    }
    .w_block {
        display: block;
        position: relative;
        width: auto;
        background-color: #f8f8f8;
        padding: 2% 3% 2.5%;
        border: 1px solid #ddd;
        border-radius: 3px;
        margin-bottom: 3.5%;
        &:last-child {
            margin-bottom: 0;
        }
    }
    .svgHolder {
        position: relative;
        & > svg {
            @include HVcenter(100%, 100%);
        }
    }
/*-------------------------------  structure  ------------------------------------*/
    .wWrapper {
        min-height: 100%;
        height: auto !important;
        height: 100%;
        position: relative;
        overflow: hidden;
    }
    .wSize {
        max-width: 1330px;
        padding: 0 10px;
        margin: 0 auto;
        position: relative;
    }
    .wContainer {
        padding: 0px 0 70px 0;
        position: relative;
    }
/*-------------------------------  wHeader  ------------------------------------*/
    .wHeader {
        position: fixed;
        height: 90px;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 20;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        &.scroll {
            background: #0c0c0d;
        }
    }
    .logo_block {
        width: 162px;
        line-height: 90px;
        height: 90px;
        margin-right: 10px;
        .logo {
            cursor: pointer;
            line-height: 0;
            width: 100%;
            height: 35px;
            display: inline-block;
            vertical-align: middle;
        }
    }
    .phone_graf_top {
        padding-top: 35px;
        box-sizing: border-box;
    }
    .phone_top {
        display: inline-block;
        vertical-align: top;
        text-decoration: none;
        svg {
            height: 17px;
            width: 8px;
            display: inline-block;
            vertical-align: top;
            fill: #ffc707;
            margin-right: 10px;
        }
        span {
            font: 14px/17px Arial, Helvetica, sans-serif;
            color: #fff;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
            display: inline-block;
            vertical-align: top;
        }
        &:hover {
            span {
                color: #ffc707;
            }
        }
    }
    .graf_top {
        display: inline-block;
        vertical-align: top;
        margin-left: 80px;
        svg {
            height: 17px;
            width: 17px;
            float: left;
            fill: #ffc707;
            margin-right: 10px;
        }
        .graf_info {
            overflow: hidden;
            span {
                font: 14px/17px Arial, Helvetica, sans-serif;
                color: #fff;
                text-transform: uppercase;
                display: block;
                & + span {
                    margin-top: 10px;
                }
            }
        }
    }
    .moz .graf_top {
        .graf_info {
            span {
                width: 160px;
                white-space: nowrap;
            }
        }
    }
    .menu {
        line-height: 90px;
        span {
            display: inline-block;
            vertical-align: middle;
            margin-left: 4%;
            color: #fff;
            cursor: pointer;
            font: 16px/18px Arial, Helvetica, sans-serif;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
            &:hover {
                color: #ffc707;
            }
        }
    }
/*-------------------------------  wFooter  ------------------------------------*/
    .wFooter {
        position: relative;
        margin: -70px auto 0;
        height: 70px;
        background: #575e69;
        overflow: hidden;
    }
    .logo_f_block {
        line-height: 70px;
        img {
            display: inline-block;
            vertical-align: middle;
        }
        span {
            display: inline-block;
            vertical-align: middle;
            color: #b5b5b5;
            font: 12px/15px Arial, Helvetica, sans-serif;
            margin-left: 30px;
        }
    }
    .wezom_block {
        line-height: 70px;
    }
    .wezom {
        display: inline-block;
        vertical-align: middle;
        text-decoration: none;
        height: 14px;
        svg {
            width: 40px;
            height: 14px;
            display: inline-block;
            vertical-align: top;
            fill: #fff;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
            margin-right: 5px;
        }
        span {
            display: inline-block;
            vertical-align: top;
            color: #b5b5b5;
            font: 12px/14px Arial, Helvetica, sans-serif;
            span {
                text-decoration: underline;
            }
        }
        &:hover {
            svg {
                fill: #f00;
            }
        }
    }
/*-------------------------------  wContainer  ------------------------------------*/
    /*-------------------------------  pageTitles  ------------------------------------*/
        .pageTitle{
            font-family: 'PFAgoraSans', Arial, Helvetica, sans-serif;
            font-size: 50px;
            line-height: 60px;
            color: #474747;
            text-align: center;
            text-transform: uppercase;
        }
        .pageTitle.w_middle,
        .pageTitle.w_small {
            margin-top: $emSizePart;
            border-bottom-width: 0;

        }
        .pageTitle.w_middle {
            font-size: $fontSizeH1 * 0.7;
        }
        .pageTitle.w_small {
            font-size: 32px;
            line-height: 40px;
        }
        .pageTitle.underlined{
            border-bottom: 1px solid $colorMiddleLight;
        }

    /*-------------------------------  wLeft  ------------------------------------*/
        .wLeft {
            position: relative;
            float: left;
            width: 25%;
            margin-right: 3%;
            margin-bottom: 3%;
        }
    /*-------------------------------  wRight  ------------------------------------*/
        .wRight {
            position: relative;
            float: right;
            width: 25%;
            margin-left: 3%;
            margin-bottom: 3%;
        }
    /*-------------------------------  wMiddle  ------------------------------------*/
        .wLeft + .wMiddle,
        .wRight + .wMiddle {
            overflow: hidden;
        }
        .wMiddle {
            position: relative;
        }
/*  top  */
    .top_block {
        background: url(../pic/top.jpg) top center no-repeat;
        height: 860px;
        position: relative;
        .wSize {height: 100%;}
    }
    .detals {
        position: absolute;
        bottom: -170px;
        left: 0;
    }
    .top_form {
        right: 0;
        bottom: -80px;
        position: absolute;
        width: 320px;
        background: #393c41;
        padding: 30px 20px;
        box-sizing: border-box;
        border-radius: 2px;
    }
    .form_title {
        color: #ffde00;
        font: 32px/32px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-align: center;
        margin-bottom: 15px;
        text-transform: uppercase;
    }
    .counter_block {
        font-size: 0;
        .spiner {
            width: 50px;
            height: 50px;
            border-radius: 3px;
            box-sizing: border-box;
            display: inline-block;
            vertical-align: top;
            border: 1px solid #959595;
            cursor: pointer;
            &.plus {
                background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAkAAAAPCAQAAACcwS2GAAAAbElEQVQoz23QQQqDUAxF0ed2FIuCg4IgnRQUcaDUlbkMXUw38fnWTdxOE5XMDiF5idhokC3x4kftSbyJPD2JloPKk+jZKT2JkcDDk/gQyTyJhS+JhZlIarsmArmdNbBT2I3dOdcl/c2N6/kTf0zCmmvIjuj6AAAAAElFTkSuQmCC) center center no-repeat;
                margin-left: 5px;
            }
            &.minus {
                background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAPCAQAAABzA0a4AAAAcElEQVQoz22PQQqDQBAEh+y6QvTo+/KL/Ee9RMH1YiQGxEf5gfIwyCjKnLpoeroFOdyT/CwX3iYDExXO5ECLQ1R6IhGPKHA0fAnqFR6U/En3LOHFSmG/bhyC48NoGYo6ehIDlx6KftTWVLfMxy2Ksg2kNolwTHzyWgAAAABJRU5ErkJggg==) center center no-repeat;
                margin-right: 5px;
            }
        }
        .counter {
            display: inline-block;
            vertical-align: top;
            width: calc(100% - 110px);
            input {
                text-align: center;
            }
        }
    }
    .top_form .wFormRow:last-child {
        margin-top: 20px;
    }
    .site_name {
        padding-top: 14%;
        text-align: center;
        color: #fff;
        font: 38px/54px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-transform: uppercase;
        span {
            display: block;
            font-size: 80px;
            line-height: 80px;
            span {
                color: #ffde00;
            }
        }
    }
/*  advantages  */
    .advantages {
        background: #fff url(../pic/block2.jpg) top center no-repeat;
        padding: 220px 0 0px;
        box-sizing: border-box;
    }
    .adv_unic {
        padding: 0 7%;
        box-sizing: border-box;
        .pageTitle {
            margin-bottom: 50px;
        }
    }
    .unic_list {
        font-size: 0;
        margin-bottom: 70px;
    }
    .unic_item {
        width: 48%;
        display: inline-block;
        vertical-align: top;
        margin: 0 1%;
        .svg_unic {
            width: 45px;
            svg {
                height: 30px;
                width: 30px;
                fill: #ffc600;
            }
        }
        span {
            color: #474747;
            font: 14px/24px Arial, Helvetica, sans-serif;
            display: block;
        }
    }
    .sale_block {
        height: 310px;
        background: #393c41;
        padding: 30px 7%;
        box-sizing: border-box;
        margin-bottom: 110px;
        position: relative;
        width: 100%;
    }
    .sale_title {
        text-align: center;
        color: #b5b5b5;
        font: 24px/24px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        margin-bottom: 40px;
        span {
            color: #ffde01;
            font-size: 32px;
            text-transform: uppercase;
        }
    }
    .sale_form {
        font-size: 0;
        margin-bottom: 37px;
        .wFormRow {
            width: 25%;
            padding: 0 10px;
            box-sizing: border-box;
            display: inline-block;
            vertical-align: top;
            margin-top: 0;
            margin-bottom: 0;
            &:last-child {margin-top: 29px;}
            &:nth-last-child(2) {margin-top: 29px;}
        }
        .real_price {
            color: #fff;
            font: 34px/50px 'PFAgoraSans', Arial, Helvetica, sans-serif;
            span {
                font-size: 74px;
            }
        }
        .old_price {
            position: absolute;
            bottom: 100%;
            left: 10px;
            color: #b4b4b4;
            font: 16px/24px 'PFAgoraSans', Arial, Helvetica, sans-serif;
            span {
                font-size: 30px;
            }
            &:after {
                top: 50%;
                left: 50%;
                position: absolute;
                content: '';
                width: 115px;
                height: 25px;
                margin-top: -12px;
                margin-left: -57px;
                background: url(data:image/png;base64,) 0px 0px no-repeat;
            }
        }
    }
    .timer_title {
        color: #b4b4b4;
        font: 24px/24px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-align: center;
        margin-bottom: 15px;
    }
    .timer {
        text-align: center;
        margin: 0 auto;
        display: block;
        width: 540px;
    }
    .adv_bot {
        background: url(../pic/adv_bot.jpg) top center no-repeat;
        -webkit-background-size: auto 100%;
        background-size: auto 100%;
        height: 470px;
    }
    .adv_bot_text {
        margin: 0 auto;
        max-width: 1100px;
        position: relative;
        height: 100%;
    }
    .adv_bot_items {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        -webkit-transform: translate(0,-50%);
        transform: translate(0,-50%);
        font-size: 0;
    }
    .adv_bot_item {
        display: inline-block;
        vertical-align: top;
        width: calc(50% - 8px);
        & + .adv_bot_item {
            margin-left: 8px;
        }
    }
    .adv_bot_item_title {
        color: #ffde00;
        margin-bottom: 20px;
        font: 32px/40px 'PFAgoraSans', Arial, Helvetica, sans-serif;
    }
    .adv_bot_item_text {
        color: #fff;
        font: 16px/24px Arial, Helvetica, sans-serif;
    }
    .adv_khars {
        margin-bottom: 30px;
    }
    .adv_khar {
        padding: 30px 0;
        box-sizing: border-box;
        margin-bottom: 30px;
        &.w_right {
            .adv_khar_img {
                float: right;
            }
        }
        &.w_left {
            .adv_khar_img {
                float: left;
            }
        }
    }
    .adv_khar_img {
        width: 58%;
        padding: 0 7%;
        box-sizing: border-box;
    }
    .avd_opus_title {
        color: #474747;
        font: 32px/40px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-transform: uppercase;
        margin-bottom: 30px;
    }
    .avd_opus_item {
        margin-bottom: 15px;
        span {
            overflow: hidden;
            display: block;
            font: 16px/24px Arial, Helvetica, sans-serif;
            color: #474747;
        }
    }
    .svg_opus {
        width: 50px;
        float: left;
        &.no-svg {
            width: 20px;
            height: 20px;
            position: relative;
            &:after {
                content: '';
                height: 6px;
                width: 6px;
                position: absolute;
                top: 8px;
                left: 0;
                background: #ffc600;
            }
        }
        svg {
            height: 30px;
            width: 30px;
            fill: #ffc600;
        }
    }
/*  compare  */
    .compare {
        background: url(../pic/block4.jpg) top center no-repeat;
        min-height: 950px;
        .pageTitle {
            margin-bottom: 20px;
        }
    }
    .compare_list {
        margin-bottom: 60px;
        font-size: 0;
    }
    .compare_item {
        display: inline-block;
        vertical-align: top;
        width: 32.5%;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        background: #fff;
        & + .compare_item {
            margin-left: 1.25%;
        }
    }
    .compare_img {
        width: 100%;
        padding-top: 62%;
        overflow: hidden;
        position: relative;
        img {
            position: absolute;
            top: 0;
            left: 50%;
            -webkit-transform: translate(-50%,0);
            transform: translate(-50%,0);
            max-width: 150%;
        }
    }
    .compare_info {
        padding: 20px 0;
        box-sizing: border-box;
    }
    .compare_name {
        padding: 8px 7%;
        box-sizing: border-box;
        min-height: 46px;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        color: #474747;
        font: 24px/30px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-transform: uppercase;
    }
    .compare_text {
        height: 155px;
        border-bottom: 1px solid rgba(0,0,0,0.1);
        color: #474747;
        font: 16px/24px Arial, Helvetica, sans-serif;
        padding: 10px 0;
        margin: 0px 7%;
        box-sizing: border-box;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
        margin-bottom: 20px;
        span {
            max-height: 120px;
            overflow: hidden;
            display: block;
        }
    }
    .compare_opus {
        margin-bottom: 15px;
        font: 14px/24px Arial, Helvetica, sans-serif;
        color: #b5b5b5;
        padding: 0 7%;
        box-sizing: border-box;
        span {
            font: 20px/24px 'PFAgoraSans', Arial, Helvetica, sans-serif;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
            color: #474747;
            display: block;
            text-transform: uppercase;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    .compare_item {
        &.cur {
            background: #393c41;
            .compare_name {
                background: #ffde00;
            }
            .compare_text {
                color: #fff;
                border-bottom: 1px solid rgba(255,255,255,0.1);
            }
            .compare_opus {
                span {
                    color: #ffde00;
                }
            }
        }
    }
    .order_block {
        width: 100%;
        background: #393c41;
        min-height: 230px;
        padding: 30px 8%;
        box-sizing: border-box;
        margin-bottom: 60px;
    }
    .order_title {
        text-align: center;
        color: #ffde01;
        font: 32px/24px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        margin-bottom: 30px;
    }
    .order_form {
        font-size: 0;
        .wFormRow {
            display: inline-block;
            vertical-align: top;
            width: 20%;
            padding: 0 5px;
            box-sizing: border-box;
            margin-top: 0;
            margin-bottom: 0;
            &:last-child {
                margin-top: 30px;
            }
        }
    }
/*  reviews  */
    .reviews {
        background: url(../pic/block5.jpg) top center no-repeat;
        -webkit-background-size: auto 100%;
        background-size: auto 100%;
        min-height: 710px;
        padding: 120px 0 100px;
        box-sizing: border-box;
    }
    .reviews_title {
        color: #ffde00;
        font: 50px/60px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-align: center;
        margin-bottom: 40px;
        text-transform: uppercase;
    }
    .reviews_slider_block {
        max-width: 880px;
        margin: 0 auto;
        position: relative;
        &:after {
            position: absolute;
            content: '';
            width: 40px;
            height: 31px;
            background: url(data:image/png;base64,) 0px 0px no-repeat;
            top: 120px;
            right: -60px;
        }
        &:before {
            position: absolute;
            content: '';
            width: 40px;
            height: 31px;
            background: url(data:image/png;base64,) 0px 0px no-repeat;
            bottom: 40px;
            left: -60px;
        }
    }
    .reviews_slider {
        display: block;
        padding-bottom: 30px;
        box-sizing: border-box;
        position: relative;
        overflow: hidden;
        li {
            float: left;
            text-align: center;
        }
    }
    .review_photo {
        height: 124px;
        width: 124px;
        overflow: hidden;
        border-radius: 124px;
        margin: 0 auto 10px;
    }
    .review_name {
        color: #b5b5b5;
        font: 14px/20px Arial, Helvetica, sans-serif;
        text-align: center;
        margin-bottom: 15px;
    }
    .review_text {
        color: #fff;
        font: 16px/24px Arial, Helvetica, sans-serif;
        height: 144px;
        overflow: hidden;
    }
    .pagg {
        position: absolute;
        bottom: 0;
        left: 50%;
        -webkit-transform: translate(-50%,0);
        transform: translate(-50%,0);
        a {
            font-size: 0;
            height: 12px;
            width: 12px;
            margin: 0 3px;
            border: 2px solid #b5b5b5;
            box-sizing: border-box;
            -webkit-transition: all 0.2s ease;
            transition: all 0.2s ease;
            border-radius: 12px;
            display: inline-block;
            vertical-align: top;
            &:hover {
                border-color: #ffde00;
            }
            &.selected {
                -webkit-transform: scale(1.2);
                transform: scale(1.2);
                border-color: #ffde00;
            }
        }
    }
/*  buy  */
    .buy {
        background: #fff url(../pic/block6.jpg) top center no-repeat;
        min-height: 860px;
        padding:  60px 0 0;
        box-sizing: border-box;
    }
    .buy_title {
        color: #474747;
        font: 50px/60px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 60px;
        span {            
            font-size: 74px;
        }
    }
    .buy_list {
        margin-bottom: 60px;
        text-align: center;
        font-size: 0;
    }
    .buy_item {
        margin-bottom: 60px;
        text-align: center;
        font-size: 0;
        width: 32%;
        display: inline-block;
        vertical-align: top;
        margin-bottom: 20px;
        padding: 0 10px;
        box-sizing: border-box;
        & + .buy_item {
            margin-left: 1.5%;
        }
    }
    .buy_svg {
        margin: 0 auto 30px;
        line-height: 0;
        svg {
            fill: #ffc707;
        }
        &.icon_money {
            svg {
                width: 143px;
                height: 162px;
            }
        }
        &.icon_dollar {
            svg {
                width: 180px;
                height: 162px;
            }
        }
        &.icon_prosent {
            svg {
                width: 204px;
                height: 162px;
            }
        }
        &.icon_post {
            svg {
                width: 158px;
                height: 128px;
            }
        }
        &.icon_box {
            svg {
                width: 125px;
                height: 121px;
            }
        }
    }
    .buy_name {
        color: #474747;
        font: 24px/32px 'PFAgoraSans', Arial, Helvetica, sans-serif;
        text-transform: uppercase;
        margin-bottom: 25px;
    }
    .buy_text {
        color: #474747;
        font: 16px/24px Arial, Helvetica, sans-serif;
    }
    .buy2 {
        background: url(../pic/block7.jpg) top center no-repeat;
        min-height: 420px;
        padding: 90px 0;
        box-sizing: border-box;
    }
