.wHeaderFix {
    position: fixed;
    text-align: center;
    height: 40px;
    padding: 5px;
    box-sizing: border-box;
    background: #0c0c0d;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: -1;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    .menu span {
        width: auto !important;
        height: 30px;
        padding: 0 5px;
        border-radius: 3px;
        margin: 0 2px;
        background: #ffde00;
        color: #393c41;
        line-height: 30px;
        font-size: 10px;
        float: none;
        vertical-align: top;
    }
    &.scroll {
        opacity: 1;
        z-index: 20;
    }
}